<template>
  <div>
    <h2>{{ ciudad }}</h2>
    <p>Región: {{ region }}</p>
    <p>País: {{ pais }}</p>
    <p>Latitud: {{ latitud }}</p>
    <p>Longitud: {{ longitud }}</p>
    <p>Temperatura: {{ temperatura }}°C</p>
    <p>Condición: {{ condicion }}</p>
    <p>Velocidad del viento: {{ velocidadViento }} km/h</p>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ciudad: '',
      region: '',
      pais: '',
      latitud: 0,
      longitud: 0,
      temperatura: 0,
      condicion: '',
      velocidadViento: 0,
    };
  },
  mounted() {
    this.fetchWeatherData();
  },
  methods: {
    fetchWeatherData() {
      const weatherApiUrl = 'http://api.weatherapi.com/v1/current.json';
      const apiKey = '8f2de44a3bab4bfba52163352232906';
      const location = 'Trujillo';

      fetch(`${weatherApiUrl}?key=${apiKey}&q=${location}&lang=es&aqi=yes`)
        .then(response => response.json())
        .then(data => {
          const { location, current } = data;
          this.ciudad = location.name;
          this.region = location.region;
          this.pais = location.country;
          this.latitud = location.lat;
          this.longitud = location.lon;
          this.temperatura = current.temp_c;
          this.condicion = current.condition.text;
          this.velocidadViento = current.wind_kph;
        })
        .catch(error => {
          console.error(error);
        });
    },
  },
};
</script>
